import React, { useState } from "react";
import {
  Button,
  Container,
  Row,
  Col,
  DropdownButton,
  Dropdown,
} from "react-bootstrap";
import { Link } from "react-router-dom";
import data from "./../data.json";

const Home = () => {
  const [bookTitle, setBookTitle] = useState("Book Title");
  function selectDropDwon(item) {
    setBookTitle(item);
  }
  let timerlink = `/timer/${bookTitle}`;
  return (
    <Container>
      <Row>
        <Col>
          <div className="Book-Title">GIT BOOK</div>
        </Col>
      </Row>
      <br />
      <Row>
        <Col>
          <DropdownButton
            id="dropdown-basic-button"
            title={bookTitle}
            onSelect={selectDropDwon}
            className="d-grid gap-2"
          >
            {data.bookList.map((item) => {
              return (
                <Dropdown.Item eventKey={item.bookTitle}>
                  {item.bookTitle}
                </Dropdown.Item>
              );
            })}
          </DropdownButton>
        </Col>
      </Row>
      <br />
      <Row>
        <Col>
          <Link to={{ pathname: timerlink }} className="d-grid gap-2">
            <Button>OK</Button>
          </Link>
        </Col>
        <Col>
          <Link to={{ pathname: "/add" }} className="d-grid gap-2">
            <Button>ADD</Button>
          </Link>
        </Col>
      </Row>
      <br />
      {/* <Row>
        <Col className="d-grid gap-2">
          <Button>Dashboard</Button>
        </Col>
      </Row> */}
    </Container>
  );
};

export default Home;
